import { AxiosRequestConfig } from 'axios';
import https from 'https';

/**
 * Shared https agent
 * AGENT_KEEP_ALIVE is the amount of time, in ms, that the socket connection will be kept open
 * Setting default to 3 mins
 */
const agent = new https.Agent({ keepAlive: true, keepAliveMsecs: Number(process.env.AGENT_KEEP_ALIVE || 300000) });

/**
 * Sets the https agent for the request for server only
 */
export const HttpsRequestInterceptor = (request: AxiosRequestConfig) => {
    request.httpsAgent = agent;

    return request;
};
